import { FC } from "react";
import classNames from "classnames/bind";

import { PageTheme } from "common/constants/enum";
import { TextWithIcon } from "common/components/TextWithIcon";
import ShieldIcon from "common/components/Icons/icons/48/shield.svg";
import ShieldMonoIcon from "common/components/Icons/icons/48/shield-mono.svg";
import LockIcon from "common/components/Icons/icons/48/lock.svg";
import LockMonoIcon from "common/components/Icons/icons/48/lock-mono.svg";
import LockMonoUKIcon from "common/components/Icons/icons/48/lock-mono-uk.svg";
import HandIcon from "common/components/Icons/icons/48/hand.svg";
import HandMonoIcon from "common/components/Icons/icons/48/hand-mono.svg";
import HouseIcon from "common/components/Icons/icons/48/house.svg";
import HouseMonoIcon from "common/components/Icons/icons/48/house-mono.svg";
import { Text } from "common/components/Text";
import { Picture } from "common/components/Picture";

import { AdvantagesBlockProps } from "./AdvantagesBlock.types";
import styles from "./AdvantagesBlock.module.scss";

const cx = classNames.bind(styles);

export const AdvantagesBlock: FC<AdvantagesBlockProps> = ({
    first,
    second,
    theme = PageTheme.RED,
    className,
    isFirstImageHorizontal,
}) => {
    const renderListItem = (item: string, index: number) => (
        <li key={index}>{item}</li>
    );

    const renderIcon = (iconName?: string) => {
        switch (iconName) {
            case "shieldMono48":
                return <ShieldMonoIcon />;
            case "shield48":
                return <ShieldIcon />;
            case "lockMono48":
                return <LockMonoIcon />;
            case "lock48":
                return <LockIcon />;
            case "lockMonoUk48":
                return <LockMonoUKIcon />;
            case "handMono48":
                return <HandMonoIcon />;
            case "hand48":
                return <HandIcon />;
            case "houseMono48":
                return <HouseMonoIcon />;
            case "house48":
                return <HouseIcon />;
            default:
                return null;
        }
    };

    return (
        <section className={cx("root", className, `theme_${theme}`)}>
            <div className={cx("content")}>
                <div className={cx("first")}>
                    {/* TODO: replace with new Heading */}
                    <Text className={cx("title", "first__title")}>
                        {first.title}
                    </Text>
                    <Picture
                        className={cx("first__image", {
                            "first__image_is-horizontal": isFirstImageHorizontal,
                        })}
                        classNameImg={cx("first__image-tag")}
                        desktop={{
                            ...first.image.desktop,
                            media: "(min-width: 1024px)",
                        }}
                    />
                    <ul className={cx("first__list")}>
                        {first.list.map(renderListItem)}
                    </ul>
                </div>
                <div className={cx("second")}>
                    {/* TODO: replace with new Heading */}
                    <Text className={cx("title", "second__title")}>
                        {second.title}
                    </Text>
                    <div className={cx("second__list")}>
                        {second.list.map((item, index) => (
                            <TextWithIcon
                                {...item}
                                icon={renderIcon(item.icon as string)}
                                reverse
                                key={index}
                            />
                        ))}
                    </div>
                </div>
            </div>
        </section>
    );
};
